<template>
  <article class="list wh100">
    <section class="section" v-for="(n,index) in showList" :key="index">
      <div class="wrap">
        <div
          class="index"
          @click="handleTapIcon"
          v-longtap="handleLongtapIcon"
          :title="n.index"
        >{{n.top}}</div>
        <div
          class="title"
          @click="handleTapTitle"
        >{{n.time}}</div>
      </div>
    </section>
  </article>
</template>

<script>
export default {
  name: "collectList",
  props:{
    list:{
      type:Object
    },
    allIndex:{
      type:Number,
      defautl: 0
    }
  },
  computed:{
    showList(){
      if(this.list.all){
        if(this.list.all[this.allIndex]){
          const result = this.list.all[this.allIndex].collect.map((item,index)=>{
            const _item = JSON.parse(JSON.stringify(item));
            const temp = new Date(_item.time);
            const year = temp.getFullYear();
            const month = this.replenishZero(temp.getMonth());
            const day = this.replenishZero(temp.getDay());
            const hour = this.replenishZero(temp.getHours());
            const minutes = this.replenishZero(temp.getMinutes());
            _item.time = `${year}-${month}-${day} ${hour}:${minutes}`;
            _item.index = index;
            return _item; 
          });
          result.reverse();
          return result;
        }
      }
      return [];
    }
  },
  methods:{
    handleTapIcon(event){
      //跳转
      const index = event.target.title-0;
      this.$emit('skip',{
        allIndex:this.allIndex,
        skipIndex:index
      });
    },
    handleLongtapIcon(event){
      //删除该收藏；
      const index = event.target.title-0;
      this.$emit('remove',{
        allIndex:this.allIndex,
        removeIndex:index
      });
    },
    handleTapTitle(event){
      //跳转
      this.handleTapIcon(event);
    },
    replenishZero(num){
      return num < 10 ? '0'+num : num;
    }
  }
};
</script>

<style scoped lang="less">
@import url("../assets/list.less");
</style>
